import React, { useState, useEffect } from 'react';
import { Typography, Button, Container, TextField } from "@mui/material";
import ArrowForwardIosOutlinedIcon from '@mui/icons-material/ArrowForwardIosOutlined';
import { makeStyles } from "@mui/styles";
import { useNavigate } from "react-router-dom";

const useStyles = makeStyles({
    botton_1: {
        fontSize: "20px !important",
        backgroundColor: '#333'
    }
});

export default function Secure() {
  const classes = useStyles();
  const navigate = useNavigate();
  const [title, setTitle] = useState('');
  const [details, setDetails] = useState('');
  const [titleErrors, setTitleErrors] = useState(false);
  const [detailErrors, setDetailErrors] = useState(false);
  const [authenticated, setAuthenticated] = useState(false);

  const handleSubmit = (e) => {
      e.preventDefault();
      setTitleErrors(false);
      setDetailErrors(false);

      if (title === '') {
          setTitleErrors(true);
      }
      if (details === '') {
          setDetailErrors(true);
      }
      if (title && details) {
          fetch('https://api.jsonstorage.net/v1/json/be8e5401-c3c6-4f0e-80e3-b2c5c298b404/d4fdaa2a-db44-423e-a7de-4e9a9caf2fe2')
              .then(response => response.json())
              .then(data => {
                  const newObject = { title, details };
                  data.news.push(newObject);

                  fetch('https://api.jsonstorage.net/v1/json/be8e5401-c3c6-4f0e-80e3-b2c5c298b404/d4fdaa2a-db44-423e-a7de-4e9a9caf2fe2?apiKey=140804b5-11a9-4b48-b42c-fb96ca00f76c', {
                      method: 'PUT',
                      headers: {
                          'Content-Type': 'application/json'
                      },
                      body: JSON.stringify(data)
                  })
                      .then(response => {
                          if (response.ok) {
                              console.log('  ');
                              navigate('/news');
                          } else {
                              console.error('   :', response.statusText);
                          }
                      })
                      .catch(error => {
                          console.error('   :', error);
                      });
              })
              .catch(error => {
                  console.error('   :', error);
              });
      }
  }

  useEffect(() => {
      const password = "BigP2007";
      const enteredPassword = prompt("Enter password:");

      if (enteredPassword === password) {
          setAuthenticated(true);
      } else {
          navigate('/'); // Redirect or handle unauthorized access
      }
  }, [navigate]);

  return authenticated ? (
      <Container>
          <Typography noWrap color="primary" variant="h3">
               
          </Typography>
          <form noValidate autoComplete="off" onSubmit={handleSubmit}>
              <TextField
                  onChange={(e) => setTitle(e.target.value)}
                  variant="outlined"
                  label=""
                  required
                  fullWidth
                  error={titleErrors}
              />
              <br /><br />
              <TextField
                  onChange={(e) => setDetails(e.target.value)}
                  variant="outlined"
                  label=""
                  required
                  fullWidth
                  rows={4}
                  error={detailErrors}
              />
              <br /><br />
              <Button
                  className={classes.botton_1}
                  variant="contained"
                  type="submit"
                  endIcon={<ArrowForwardIosOutlinedIcon fontSize="medium" />}
              >
                  
              </Button>
          </form>
      </Container>
  ) : null;
}