import React, { useEffect, useRef, useState } from "react";
import { makeStyles } from "@mui/styles";
import { Drawer, List, ListItem, ListItemIcon, ListItemText, Typography, AppBar, Toolbar, IconButton, useMediaQuery, ThemeProvider, createTheme } from "@mui/material";
import { SubjectOutlined, AddCircleOutlineOutlined } from "@mui/icons-material";
import MenuIcon from '@mui/icons-material/Menu';
import Header from "../Header/Header";
import AnnouncementIcon from '@mui/icons-material/Announcement';
import EmojiEventsIcon from '@mui/icons-material/EmojiEvents';
import ArticleIcon from '@mui/icons-material/Article';
import ChecklistIcon from '@mui/icons-material/Checklist';
import InfoIcon from '@mui/icons-material/Info';
import { useLocation, useNavigate } from "react-router-dom";

var drawerWidth = 240;
const useStyles = makeStyles((theme) => {
        return {
            page: {
                background: '#f9f9f9',
                width: '100vw',
                flexGrow: 1,
                padding: theme.spacing(3),
                transition: 'margin-left 0.2s ease',
                marginLeft: -drawerWidth
            },
            appBar: {
                background: "#1C1C84",
                transition: 'margin-left 0.2s ease',
                marginLeft: drawerWidth
            },
            pageShift: {
                width: '100vw',
                background: '#f9f9f9',
                padding: theme.spacing(3),
                marginLeft: 0,
                transition: 'margin-left 0.2s ease',
            },
            appBarShift: {
                background: "#1C1C84",
                marginLeft: 0,
                transition: 'margin-left 0.2s ease',
                width: '97%',
            },
            drawer: {
                width: drawerWidth,
            },
            drawerPaper: {
                width: drawerWidth,
            },
            drawerPaperBlack:{
                width: drawerWidth,
            },
            root: {
                display: 'flex',
            },
            toolbar: theme.mixins.toolbar,
            title: {
                flexGrow: 1,
            },
            icon: {
                marginLeft: "50%"
            },
            '@media screen and (max-width: 1150px)': {
                pageShift: {
                    marginLeft: -drawerWidth,
                },
                page: {
                    padding: theme.spacing(0)
    
                },
            }
        };
    }
);


    

const Layout = ({ children, themeMode, toggleTheme }) => {
    const classes = useStyles();
    const navigate = useNavigate();
    const location = useLocation();
    const [isTournamentsPage, setIsTournamentsPage] = useState(false);
    const [isDrawerOpen, setIsDrawerOpen] = useState(false);
    const drawerRef = useRef(null); //  ref  Drawer
    const toggleDrawer = () => {
        setIsDrawerOpen(!isDrawerOpen);
    };

    useEffect(() => {
        setIsTournamentsPage(location.pathname === '/tournaments');
    }, [location.pathname]);
    const lightTheme = createTheme({
        palette: {
          mode: 'light',
          primary: {
            main: '#333'
          },
          secondary: {
            main: '#fff'
          },
        }
      });
      
      const darkTheme = createTheme({
        palette: {
          mode: 'dark',
          primary: {
            main: '#333'
          },
          secondary: {
            main: '#fff'
          },
        }
      });

    const menu = [
        {
            text: "",
            icon: <SubjectOutlined color={isTournamentsPage ? "secondary" : "primary"} />,
            path: "/"
        },
        {
            text: "",
            icon: <ArticleIcon color={isTournamentsPage ? "secondary" : "primary"} />,
            path: "/notes"
        },
        {
            text: "",
            icon: <AnnouncementIcon color={isTournamentsPage ? "secondary" : "primary"} />,
            path: "/news"
        },
        {
            text: "",
            icon: <EmojiEventsIcon color={isTournamentsPage ? "secondary" : "primary"} />,
            path: "/tournaments"
        }
    ];

    const mobileMenu = [
        {
            text: "",
            icon: <SubjectOutlined color={isTournamentsPage ? "secondary" : "primary"} />,
            path: "/"
        },
        {
            text: "",
            icon: <ArticleIcon color={isTournamentsPage ? "secondary" : "primary"} />,
            path: "/notes"
        },
        {
            text: "",
            icon: <AnnouncementIcon color={isTournamentsPage ? "secondary" : "primary"} />,
            path: "/news"
        },
        {
            text: " ",
            icon: <InfoIcon color={isTournamentsPage ? "secondary" : "primary"} />,
            path: "/about"
        },
        {
            text: " ",
            icon: <ChecklistIcon color={isTournamentsPage ? "secondary" : "primary"} />,
            path: "/goals"
        },
        {
            text: "",
            icon: <EmojiEventsIcon color={isTournamentsPage ? "secondary" : "primary"} />,
            path: "/tournaments"
        }
    ];

    const isMobile = useMediaQuery('(max-width:1150px)'); //  -

    const menuItems = isMobile ? mobileMenu : menu;

    return (
        <ThemeProvider theme={isTournamentsPage ? darkTheme : lightTheme}>
        <div className={classes.root}>
            <AppBar>
                <Toolbar className={isDrawerOpen ? classes.appBar : classes.appBarShift}>
                    <IconButton
                        color="inherit"
                        aria-label="open drawer"
                        edge="start"
                        onClick={toggleDrawer}
                    >
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" className={classes.title}>Big Tournaments</Typography>
                    <Header />
                </Toolbar>
            </AppBar>

            <Drawer
                className={classes.drawer}
                variant="persistent"
                anchor={isMobile ? "top" : "left"} //       
                open={isDrawerOpen}
                classes={{
                    paper: isMobile ? classes.drawerPaperMobile : classes.drawerPaper,
                }}
            >
                <Typography variant="h3" color="primary">BigT</Typography >
                <List>
                    {menuItems.map((item) => (
                        <ListItem button key={item.text} onClick={() => {
                            navigate(item.path); //   
                            if(isMobile) {
                                toggleDrawer(); //  Drawer   
                            }
                        }}>
                            <ListItemIcon>{item.icon}</ListItemIcon>
                            <ListItemText primary={item.text} />
                        </ListItem>
                    ))}
                </List>
            </Drawer>

                <div className={isDrawerOpen ? classes.pageShift : classes.page} style={{ background: isTournamentsPage ? '#1F0C19' : '#f9f9f9' }}>
                    <div className={classes.toolbar} />
                    {children}
                </div>

            </div>
        </ThemeProvider>
    );
};

export default Layout;