import { HashRouter as Router, Routes, Route } from "react-router-dom";
import Notes from "./pages/Notes/Notes";
import Secure from "./pages/Secure/Secure";
import Home from "./pages/Home/Home";
import About from "./pages/About/About";
import Goals from "./pages/Goals/Goals";
import News from "./pages/News/News";
import Soccer from "./pages/Soccer/Soccer";
import Basketball from "./pages/Basketball/Basketball";
import Volleyball from "./pages/Volleyball/Volleyball";
import Csgo from "./pages/Csgo/Csgo";
import Tournaments from "./pages/Tournaments/Tournaments";
import { createTheme, ThemeProvider } from "@mui/material";
import Layout from "./components/Layout/Layout";

const theme = createTheme({
  palette: {
    mode: 'light', //   
    primary: {
      main: '#333'
    },
    secondary: {
      main: '#fff'
    },
  }
});

function App() {
  return (
    <ThemeProvider theme={theme}>
      <Router>
        <Layout>
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/about" element={<About />} />
            <Route path="/goals" element={<Goals />} />
            <Route path="/notes" element={<Notes />} />
            <Route path="/secure" element={<Secure />} />
            <Route path="/news" element={<News />} />
            <Route path="/soccer" element={<Soccer />} />
            <Route path="/basketball" element={<Basketball />} />
            <Route path="/volleyball" element={<Volleyball />} />
            <Route path="/tournaments" element={<Tournaments />} />
            <Route path="/csgo" element={<Csgo />} />
          </Routes>
        </Layout>
      </Router>
    </ThemeProvider>
  );
}

export default App;
