import React, { useState, useEffect } from 'react';
import './Notes.scss';
import { Grid, Paper, Typography, Button } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import { makeStyles } from '@mui/styles';


const useStyles = makeStyles(() => {
  return ({
    button: {
      width: "20%",
      background: "#1C1C84 !important",
    },
  });
});

export default function Notes() {
  const classes = useStyles();
  const [petitions, setPetitions] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('https://api.jsonstorage.net/v1/json/be8e5401-c3c6-4f0e-80e3-b2c5c298b404/36cdb5ce-1350-429b-b30f-25054dfa2ead?apiKey=140804b5-11a9-4b48-b42c-fb96ca00f76c');
        const data = await response.json();
        setPetitions(data.petitions.reverse());
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    fetchData();
  }, []);

  return (
    <div>
      <Grid container spacing={3} class="petition-wrapper">
      <div className="text_buton_wrapper">
        <Typography variant='h2' className="petition_description_of_the_page"> </Typography>
        <Button
          onClick={(event) => {
            event.preventDefault(); //    
            window.open("https://t.me/makePetitionBot");
          }}
          type="button"
          className={classes.button}
          variant='contained'
          endIcon={<AddIcon fontSize='large'/>}
        >
          <Typography variant='h6'></Typography>
        </Button>
      </div>
      
        {petitions.map((petition, index) => (
          <div  key={index}>
            <Paper className="petition" >
              <div className="title">
                <span><Typography variant='h5'>  ': {petition.name}</Typography></span><span>: {petition.id}</span>
              </div>
              <div className="petition_text">
                {petition.petition}
              </div>
            </Paper>
          </div>
        ))}      
      </Grid>

    </div>
  );
}
